@import '../../../assets/stylesheets/colors.scss';

.icon {
  width: 25px;
  height: 25px;
  margin-left: 10px;
  margin-bottom: -5px;
}
.primaryTitle {
  border-bottom: solid 1px;
  padding-bottom: 18px;
}
.secondaryTitle {
  margin-top: 40px;
}
.paragraphDiv {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  font-size: 22px;
}
.informationDiv {
  display: flex;
  flex-direction: row;
}
.errorIcon {
  width: 24px;
  height: 24px;
  margin-right: 9px;
  margin-top: 3px;
}
.rightSubDiv {
  display: flex;
  margin-top: 53px;
}
.tableTitle {
  margin-bottom: 19px;
}
.leftSubDiv {
  min-width: 332px;
}
.row {
  display: flex;
  flex-direction: row;
}
.column {
  display: flex;
  flex-direction: column;
}
.downloadLink {
  text-decoration: none;
  color: black;
}
.greyText {
  color: $primary30;
}
.darkDiv {
  background: $primary40;
  border-radius: 10px;
  width: 378px;
  height: 108px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;
}
.lightText {
  margin-left: 16px;
  margin-right: 16px;
  color: $white;
}
.indicatorsTable {
  width: 100%;
  cursor: pointer;
  border: 1px solid #fff;

  &:hover {
    border: 1px solid;
  }
}
.separator {
  height: 1px;
  background-color: $primary30;
  margin-bottom: 22px;
}

.select {
  width: 280px !important;
  height: 44px !important;
  border: 1px solid #7F7F7F !important;
  box-sizing: border-box !important;
  border-radius: 8px !important;
}
.collapsedContainer {
  display: flex;
  flex-direction: row;
}
.end {
  display: flex;
  align-self: flex-end;
  height: 52px;
}
.firstNumber{
  margin-right: 277px;
}
.sliderInvisibleDiv0 {
  width: 14px;
}
.sliderInvisibleDiv1 {
  width: 10px;
}
.sliderInvisibleDiv3 {
  width: 65px;
}
.hide {
  display: none;
}
.chartsDiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.mt50{
  margin-top: 50px;
}

@media print
{
    .noPrint, .noPrint *, .container, header
    {
        display: none !important;
    }

    ::-webkit-scrollbar { display: none; }

    @page {
      size: landscape;
    }

    body {
      overflow: auto !important;
      height: auto !important;
      padding-right: 0 !important;
    }

    .chart div div, .chart canvas {
      width: 100% !important;
    }

    h4 {
      font-size: 1em !important;
      margin-top: 0 !important;
    }
}