@import '../../assets/stylesheets/colors.scss';

.uploaderContainer {
  background-color: $light40;
  display: block;
  align-items: center;
  transition: background-color 0.1s ease;
  padding: 24px;
  margin-top: 24px;

  &.fileHover {
    background-color: $hover;
  }
  border-radius: 10px;
}

.fileContainer {
  margin-top: 16px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.invalidFormat {
  margin-top: 63px;
  margin-left: 70px;
  color: $error-red;
  position: absolute;
  display: flex;
  align-items: center;

  & > svg {
    display: inline-block;
    margin-right: 8px;
    width: 14px;
  }
}
.row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.text {
  justify-self: center;
  align-self: center;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  width: 246px;
}
.subText {
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 28px;
  color: $primary30;
}
